<template>
  <div class="vc-compact">
    <template v-for="(v, i) in palettes" :key="i">
      <div class="vc-compact__row">
        <template v-for="(v1, k) in v" :key="k">
          <div class="vc-compact__color-cube--wrap" @click="onColorChange(v1)">
            <div
              :class="[
                'vc-compact__color_cube',
                {
                  advance: v1 === 'advance',
                  transparent: v1 === 'transparent',
                },
              ]"
              :style="computedBgStyle(v1)"
            ></div>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>

<script lang="ts">
  import { defineComponent } from "vue";
  import tinycolor from "tinycolor2";

  const defaultColors: string[][] = [
    // 第一行
    [
      "#fcc02e",
      "#f67c01",
      "#e64a19",
      "#d81b43",
      "#8e24aa",
      "#512da7",
      "#1f87e8",
      "#008781",
      "#05a045",
    ],
    // 第二行
    [
      "#fed835",
      "#fb8c00",
      "#f5511e",
      "#eb1d4e",
      "#9c28b1",
      "#5d35b0",
      "#2097f3",
      "#029688",
      "#4cb050",
    ],
    // 第三行
    [
      "#ffeb3c",
      "#ffa727",
      "#fe5722",
      "#eb4165",
      "#aa47bc",
      "#673bb7",
      "#42a5f6",
      "#26a59a",
      "#83c683",
    ],
    // 第四行
    [
      "#fff176",
      "#ffb74e",
      "#ff8a66",
      "#f1627e",
      "#b968c7",
      "#7986cc",
      "#64b5f6",
      "#80cbc4",
      "#a5d6a7",
    ],
    // 第五行
    [
      "#fff59c",
      "#ffcc80",
      "#ffab91",
      "#fb879e",
      "#cf93d9",
      "#9ea8db",
      "#90caf8",
      "#b2dfdc",
      "#c8e6ca",
    ],
    // 最后一行
    [
      "transparent",
      "#ffffff",
      "#dedede",
      "#a9a9a9",
      "#4b4b4b",
      "#353535",
      "#212121",
      "#000000",
      "advance",
    ],
  ];

  export default defineComponent({
    name: "Palette",
    emits: ["change"],
    setup(_props, { emit }) {
      const computedBgStyle = (color: string) => {
        if (color === "transparent") {
          return color;
        }
        if (color === "advance") {
          return {};
        }
        return { background: tinycolor(color).toRgbString() };
      };

      const onColorChange = (color: string) => {
        emit("change", color);
      };

      return { palettes: defaultColors, computedBgStyle, onColorChange };
    },
  });
</script>

<style lang="scss" scoped>
  .vc-compact {
    margin-bottom: 15px;
    width: auto;
    box-shadow: 3px 0 5px rgba(0, 0, 0, 0.08);
    display: inline-block;

    &__row {
      position: relative;
      width: 100%;
      margin: 0;
      height: 27px;

      & > * {
        display: inline-block;
        vertical-align: middle;
      }
    }

    &__color_cube {
      width: 100%;
      height: 100%;

      &.transparent {
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: white;
        }

        &::after {
          content: "";
          position: absolute;
          top: 100%;
          left: 0;
          transform: rotate(-45deg);
          transform-origin: 0 0;
          width: 35px;
          height: 1px;
          background: red;
        }
      }

      &.advance {
        background: url(data:image/png;base64,);
      }

      .alpha {
        background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==);
        background-repeat: repeat;
      }
    }

    &__color-cube--wrap {
      position: relative;
      width: 27px;
      height: 27px;
      cursor: pointer;
      overflow: hidden;

      &:hover {
        transform: scale(1.2);
        z-index: 299;
        transition: transform 0.2s;
      }
    }
  }
</style>
